___CSS_LOADER_EXPORT___.push([module.id, ".modal[data-v-737250be]{background-color:rgba(0,0,0,.5);display:block;height:100%;left:0;position:fixed;top:0;width:100%;z-index:9999}.modal-content[data-v-737250be]{background-color:#fff;margin:10% auto;padding:20px;width:50%}.close-btn[data-v-737250be]{cursor:pointer;font-size:30px;position:absolute;right:10px;top:10px}", "",{"version":3,"sources":["webpack://./resources/js/components/ModalFirmantes.vue"],"names":[],"mappings":"AA6EA,wBAOE,+BAAoC,CANpC,aAAc,CAKd,WAAY,CAFZ,MAAO,CAFP,cAAe,CACf,KAAM,CAEN,UAAW,CAGX,YACF,CACA,gCACE,qBAAuB,CAEvB,eAAgB,CADhB,YAAa,CAEb,SACF,CACA,4BAKE,cAAe,CADf,cAAe,CAHf,iBAAkB,CAElB,UAAW,CADX,QAIF","sourcesContent":["<template>\r\n  <!-- Modal para visualizar los firmantes -->\r\n  <div class=\"modal fade\" id=\"firmantesModal\" tabindex=\"-1\" role=\"dialog\" aria-labelledby=\"firmantesModalLabel\" aria-hidden=\"true\" v-show=\"isOpen\">\r\n    <div class=\"modal-dialog modal-lg\" role=\"document\">\r\n      <div class=\"modal-content\">\r\n        <div class=\"modal-header\" style=\"background-color: #7e7e7e!important;color: #ffffff!important;\">\r\n          <h5 class=\"modal-title\" id=\"firmantesModalLabel\">Firmantes pendientes</h5>\r\n          <button type=\"button\" class=\"close\" @click=\"closeModal\" aria-label=\"Close\">\r\n            <span aria-hidden=\"true\">&times;</span>\r\n          </button>\r\n        </div>\r\n        <div class=\"modal-body\">\r\n          <table class=\"table table-striped mt-3\" style=\"width:100%\">\r\n            <tr v-for=\"usuario in usuarios\" :key=\"firmante.id\">\r\n              <td style=\"width: 10%;\">\r\n                <div class=\"user-icon\">\r\n                  <span class=\"material-icons\">person</span>\r\n                </div>\r\n              </td>\r\n              <td>{{ usuario.nombres }} {{ usuario.apellido_paterno }} {{ usuario.apellido_materno }}</td>\r\n              <td>{{ usuario.correo }}</td>\r\n              <td>\r\n                <div v-if=\"usuario.estado === 0\">\r\n                  <span class=\"badge badge-warning\">Pendiente</span>\r\n                  <span class=\"badge badge-primary\" @click=\"notificarFirmaPendiente(usuario.id)\">Notificar</span>\r\n                </div>\r\n                <div v-else-if=\"usuario.estado === 3\">\r\n                  <span class=\"badge badge-danger\">Rechazado</span>\r\n                  <p style=\"font-size: 12px;\">Rechazó el {{ usuario.formatted_date }}</p>\r\n                </div>\r\n                <div v-else>\r\n                  <span class=\"badge badge-success\">Firmado</span>\r\n                  <p style=\"font-size: 12px;\">Firmó el {{ usuario.formatted_date }}</p>\r\n                </div>\r\n              </td>\r\n            </tr>\r\n          </table>\r\n        </div>\r\n        <div class=\"modal-footer\">\r\n          <button type=\"button\" class=\"btn btn-secondary\" @click=\"closeModal\">Cerrar</button>\r\n        </div>\r\n      </div>\r\n    </div>\r\n  </div>\r\n</template>\r\n\r\n<script>\r\nexport default {\r\n  data() {\r\n    return {\r\n      isOpen: false,\r\n      usuarios: []  // Se inicializa vacío\r\n    };\r\n  },\r\n  methods: {\r\n    // Método para abrir el modal y cargar los firmantes\r\n    openModal(firmantes) {\r\n      this.usuarios = firmantes;\r\n      alert(JSON.stringify(this.usuarios));\r\n      alert(\"abrete modal recualiao\")\r\n      this.isOpen = true;\r\n    },\r\n    // Método para cerrar el modal\r\n    closeModal() {\r\n      this.isOpen = false;\r\n    },\r\n    // Método para notificar al firmante (esto lo puedes ajustar según tu lógica)\r\n    notificarFirmaPendiente(id) {\r\n      console.log(`Notificando firma pendiente para el firmante con ID ${id}`);\r\n      // Aquí va tu lógica para enviar la notificación\r\n    }\r\n  }\r\n};\r\n</script>\r\n\r\n\r\n<style scoped>\r\n.modal {\r\n  display: block; /* Asegúrate de que el modal sea visible */\r\n  position: fixed;\r\n  top: 0;\r\n  left: 0;\r\n  width: 100%;\r\n  height: 100%;\r\n  background-color: rgba(0, 0, 0, 0.5); /* Fondo oscuro */\r\n  z-index: 9999; /* Para que esté encima de otros elementos */\r\n}\r\n.modal-content {\r\n  background-color: white;\r\n  padding: 20px;\r\n  margin: 10% auto;\r\n  width: 50%;\r\n}\r\n.close-btn {\r\n  position: absolute;\r\n  top: 10px;\r\n  right: 10px;\r\n  font-size: 30px;\r\n  cursor: pointer;\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
